/**
 * A callback function that has the option to take in a parameter
 */
type Callback = (element?:any) => void;

/***
 * Fires a callback function when the element passed is within it's visibility
 * @param section A html element to observe
 * @param callback A callback function called when element is in view
 * @param threshold A optional override to say at what visibility ratio the callback should be fired at
 */
export const fireInView = (section:HTMLElement, callback:Callback, threshold:number = 0.5) => {
  const observer = new IntersectionObserver((entries) => {
    entries.forEach((entry) => {
      if(entry.intersectionRatio >= threshold) callback(section);
    });
  }, { threshold: threshold });
  observer.observe(section);
}

/**
 * Creates an observer to add a class to an element that passes within it's
 * visibility threshold
 * @param section A html element to observe
 * @param clazz A class to add to the section when in view
 * @param threshold A optional override to say at what visibility ratio the class should be added at
 */
export const classInView = (section:HTMLElement, clazz: string, threshold?:number) => {
  const addClass = (element:HTMLElement) => element.classList.add(clazz);
  fireInView(section, addClass, threshold);
}