import { fireInView } from "../tools/utilities/section-tools";

const SELECTOR_ODOMETER = '[data-odometer]';

const ATTR_NEW_VALUE = 'data-new-value';

const odometerSelector =
  document.querySelectorAll<HTMLElement>(SELECTOR_ODOMETER);


const updateOdometerElement = (element:HTMLElement) => {
  const newValue = element.getAttribute(ATTR_NEW_VALUE);
  if(newValue) element.innerHTML = newValue;
}

odometerSelector.forEach((odometer) => {
  fireInView(odometer, updateOdometerElement);
});